.slider {
  width: 100%;
  margin: auto;
  overflow: hidden;
}

.slider ul{
  display: -ms-flexbox;
  display: flex;
  width: 300%;
  padding: 0;
  margin: -10px;
  -webkit-animation: changeSlide 11s 1;
          animation: changeSlide 11s 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.slider li{
  list-style: none;
  width: 100%;
  background: green;
height: 800px;
}

.slider img{
  width: 100%;
margin-top: -150px;
}

@-webkit-keyframes changeSlide {
  0% {margin-left: 0;}
  30% {margin-left: 0;}

  35% {margin-left: -100%;}
  65% {margin-left: -100%;}

  70% {margin-left: -200%;}
  100% {margin-left: -200%;}

}

@keyframes changeSlide {
  0% {margin-left: 0;}
  30% {margin-left: 0;}

  35% {margin-left: -100%;}
  65% {margin-left: -100%;}

  70% {margin-left: -200%;}
  100% {margin-left: -200%;}

}



